<template>
  <div class="group-discussion">
    <head-top :title="groupName">
      <router-link
        :to="'/history/teamRank/'+$route.params.id+'/'+$route.params.groupId+'/'+$route.params.name"
      >
        <i class="iconfont iconchengyuan"></i>
      </router-link>
    </head-top>

    <!-- 头部摘要 -->
    <div class="summary">
      <div>
        得分数:
        <span>{{score}}</span>
      </div>
      <!-- <div>
        论题:
        <span>{{total}}</span>
      </div>-->
      <div>
        点赞数:
        <span>{{good}}</span>
      </div>
    </div>

    <!-- 聊天信息 -->
    <div class="chat" ref="chat">
      <!-- <div v-for="(item, index) in dataList" :key="index"> -->
      <div class="chat_title">
        <span class="chat_title_left">[论题]</span>
        <span v-if="contentType === contentTypeDefault ">{{ content }}</span>
        <img v-else
          :src="getDiscussionImage(  content)"
          :style="{maxWidth:'70vw',maxHeight:'15rem'}"
          @click="preview(getDiscussionImage(content))"
        />
        <!-- <r-image
            class="chat_img"
            width="70vw"
            height="15rem"
            :src="getDiscussionImage( item.Key['GSContent'])"
        ></r-image>-->
      </div>

      <div v-show="dataList && dataList.length>0" class="chat_content">
        <div
          v-for="(sub, k) in dataList"
          :key="k"
          :class=" sub['StudentID']!== $store.getters.studentID ? 'chat_item left_item' : 'chat_item right_item'"
        >
          <div class="chat_icon">
            <!-- {{sub['StudentPhoto']}} -->
            <img :src="defaultPhotoImg" />
          </div>

          <div class="chat_news">
            <div class="chat_news_name">{{sub['StudentName']}}</div>
            <div class="chat_news_cont">
              <span v-if="sub.Key['GDForm'] === contentTypeDefault">{{sub.Key['GDContent'] }}</span>
              <img
                v-else
                :src="getImageFile(sub.Key['GDContent'])"
                alt
                @click="preview(getImageFile(sub.Key['GDContent']))"
              />
            </div>
          </div>
        </div>
      </div>
      <!-- </div> -->
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Button, ImagePreview } from "vant";
import defaultPhoto from "@/assets/boy_img.png";

import { ContentType, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    [Button.name]: Button,
    headTop
  },
  props: {},
  data() {
    return {
      score: 0,
      // total: 0,
      good: 0,
      dataList: [],
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      content: "",
      imgBase: "",

      defaultPhotoImg: defaultPhoto,
      groupName: this.$route.params.name,
      imagePreview: null
    };
  },
  created() {
    this.getDataStatics();
    this.getDataDetail();
  },
  methods: {
    preview(image) {
      this.imagePreview = ImagePreview({
        images: [image],
        showIndex: false,
        onClose() {}
      });
    },
    getDataStatics() {
      let reqData = {
        gsID: this.$route.params.id
      };
      this.$mainHttp.groupDiscussion
        .getStatistic(reqData)
        .then(res => {
          if (res.Result) {
            if (res["TotalGood"]) this.good = res["TotalGood"] || 0;
            if (res["TotalScore"]) this.score = res["TotalScore"] || 0;
            if (res.Data) {
              this.contentType = res.Data["GSForm"];
              this.content = res.Data["GSContent"];
            }
          }
        })
        .catch(err => {});
    },
    getDataDetail() {
      this.dataList = [];
      let reqData = { gsid: this.$route.params.id };
      this.$mainHttp.groupDiscussion
        .getData(reqData)
        .then(res => {
          if (res.Result && res.GAData && res.GAData.length > 0) {
            res.GAData.forEach(item => {
              this.dataList.push(item);
            });
          }
        })
        .catch(err => {});
    },
    getDiscussionImage(fileId) {
      return this.$mainHttp.resourse.picture(
        fileId,
        ModulePicture.GroupSubject
      );
    },
    getImageFile(fileId) {
      return this.$mainHttp.resourse.picture(
        fileId,
        ModulePicture.GroupDiscuss
      );
    }
  },
  beforeDestroy() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang='less' scoped>
.group-discussion {
  width: 100%;
  height: 100%;
  background: #ebf0ef;

  .iconchengyuan {
    font-size: 1.7rem;
    position: absolute;
    top: 1.3rem;
    right: 2rem;
    color: #fff;
  }

  .summary {
    width: 93vw;
    height: 4.5rem;
    padding: 0 3.5vw;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: 1.4rem;
    font-family: MicrosoftYaHei;
    color: rgba(153, 153, 153, 1);
    line-height: 4.5rem;

    span {
      font-size: 2rem;
      font-family: DINPro-Regular;
      color: rgba(240, 122, 35, 1);
    }
  }

  .chat {
    padding: 0 3vw;
    text-align: left;
    height: calc(100vh - 4.3rem - 4.5rem);
    overflow-y: auto;

    .chat_title {
      display: flex;
      align-items: flex-start;
      margin: 0.5rem 0;

      font-size: 1.5rem;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);

      .chat_title_left {
        min-width: 5rem;
      }
      .chat_img {
        padding-top: 0.3rem;
        /deep/img {
          object-position: left;
        }
      }
    }

    .chat_content {
      .chat_item {
        width: 100%;
        margin-bottom: 1.6rem;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        .chat_icon {
          margin-right: 0.5rem;
          img {
            margin-top: 0.2rem;
            max-width: 11vw;
            max-height: 5rem;
            border-radius: 50%;
          }
        }
        .chat_news {
          max-width: 80vw;

          .chat_news_name {
            font-size: 1.2rem;
            line-height: 2;
            color: rgba(102, 102, 102, 1);
          }
          .chat_news_cont {
            background: #fff;
            border-radius: 0.5rem;
            padding: 0.5rem 1rem;
            font-size: 1.4rem;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            word-wrap: break-word;
            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }
        }
      }
      .right_item {
        flex-direction: row-reverse;
        .chat_icon {
          margin-left: 0.5rem;
        }
        .chat_news {
          .chat_news_name {
            text-align: right;
          }
          .chat_news_cont {
            background: rgba(0, 203, 171, 1);
            color: #fff;
            text-align: right;
          }
        }
      }
    }
  }
}
</style>


